import { useParams } from "react-router";
import { Button } from "../reusable_components/custom_button";

export default function UpdateBooking() {
  const { id } = useParams();
  return (
    <main className="max-w-xl mx-auto p-6 my-6 text-black bg-white">
      <div className="rounded-lg p-6 shadow-sm border border-gray-200">
        <h2 className="text-start text-black px-2 font-extrabold text-3xl">
          Update booking
          <p className="text-sm font-normal text-slate-600 pt-2">
            Trust matols logistics services to transport your items safely.
          </p>
        </h2>
      </div>

      <div className="mt-4 flex flex-col gap-4">
        <span className="text-sm text-slate-700">
          Click on the category you would like to update .
        </span>
        <Button
          onClick={() => {
            window.location.href = `/bookings/booking/${id}/update/vehicle/type`;
          }}
        >
          Vehicle type
        </Button>
        <Button
          onClick={() => {
            window.location.href = `/bookings/booking/${id}/update/routes`;
          }}
        >
          Pick up and drop off
        </Button>
        <Button
          onClick={() => {
            window.location.href = `/bookings/booking/${id}/update/additional/information`;
          }}
        >
          Additional information
        </Button>
      </div>
    </main>
  );
}
